<template>
  <div class="row mx-0">
    <div class="col-lg-7 gx-2 mb-5">
      <div class="infocard">
        <ul class="nav nav-tabs nav-fill nav-dark sticky-top mb-4 rounded">
          <li
            class="nav-item bg-light"
            v-for="(tab, index) of tabs"
            :key="tab.component"
          >
            <button
              class="nav-link"
              data-bs-toggle="tab"
              type="button"
              :id="`head-${tab.component}`"
              :data-bs-target="`#${tab.component}`"
              :class="{ active: index == 0 }"
              @click="chosenTab = tab.component"
              v-html="tab.name"
            ></button>
          </li>
        </ul>

        <div class="tab-content" id="mainContent">
          <div
            v-for="(tab, index) of tabs"
            class="tab-pane fade px-2 p-lg-0"
            :id="tab.component"
            :key="tab.component"
            :class="{ active: index == 0, show: index == 0 }"
          >
            <component
              :is="tab.component"
              :key="tab.component"
              @focusCard="focusCard"
            ></component>
          </div>
        </div>
        <div
          class="offcanvas offcanvas-bottom h-100 border-0"
          tabindex="-1"
          id="offcanvas-result"
        >
          <div class="offcanvas-header">
            <h5 class="offcanvas-title">Итог</h5>
            <button type="button" class="btn btn-primary">Создать КП</button>
          </div>
          <div class="offcanvas-body container">
            <div class="infocard">
              <result></result>
            </div>
            <div class="container mb-2 fixed-bottom">
              <button
                type="button"
                class="btn btn-danger w-100"
                data-bs-dismiss="offcanvas"
                aria-label="Close"
              >
                Закрыть
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div class="col-5 d-none d-lg-block sticky-top">
      <div class="card mb-2">
        <div class="card-header bg-card-even row gx-0">
          <ul class="nav nav-tabs card-header-tabs col">
            <li class="nav-item">
              <button
                class="nav-link active"
                id="home-tab"
                data-bs-toggle="tab"
                data-bs-target="#home"
                type="button"
                role="tab"
                aria-controls="home"
                aria-selected="true"
              >
                Итог
              </button>
            </li>
            <li class="nav-item">
              <button
                class="nav-link"
                id="salary-tab"
                data-bs-toggle="tab"
                data-bs-target="#salary"
                type="button"
                role="tab"
                aria-controls="salary"
                aria-selected="false"
              >
                Зарплата
              </button>
            </li>
          </ul>
          <div class="col-3">
            <button class="btn btn-primary p-0 w-100" @click="save">
              Сохранить
            </button>
          </div>
        </div>
        <div class="card-body px-0">
          <div class="tab-content pb-4 overflow-auto result-block">
            <div
              class="tab-pane fade show active"
              id="home"
              role="tabpanel"
              aria-labelledby="home-tab"
            >
              <result></result>
            </div>
            <div class="tab-pane fade" id="salary" role="tabpanel">
              <!-- <salary></salary> -->
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
  <div class="fixed-bottom mb-2 container d-block d-lg-none">
    <div class="row">
      <div class="col">
        <button
          class="btn btn-primary w-100"
          type="button"
          data-bs-toggle="offcanvas"
          data-bs-target="#offcanvas-result"
          v-on:touchstart.passive="save"
        >
          Итог
        </button>
      </div>
      <div class="col" v-if="chosenTab != 'logistics'">
        <button class="btn btn-primary w-100" type="button" @click="addCard">
          +
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapMutations } from "vuex";

import Materials from "./components/Materials.vue";
import Products from "./components/Products.vue";
import Logistics from "./components/Logistics.vue";

import Result from "./components/Result.vue";

export default {
  name: "App",
  data() {
    return {
      tabs: [
        {
          component: "materials",
          name: "Материал",
        },
        {
          component: "products",
          name: "Изделия",
        },
        {
          component: "logistics",
          name: "Логистика",
        },
      ],
      chosenTab: "materials",
    };
  },
  methods: {
    ...mapMutations(["addMaterialCard", "addProductCard"]),
    ...mapActions(["save"]),
    focusCard(tab, id) {
      setTimeout(function () {
        let element = document.getElementById(`${tab}-${id}`);
        element.scrollIntoView({
          block: "center",
          behavior: "auto",
        });
      }, 0);
    },
    addCard() {
      let functions = {
        materials: this.addMaterialCard,
        products: this.addProductCard,
      };
      functions[this.chosenTab]();
    },
  },
  components: {
    Materials,
    Products,
    Logistics,
    Result,
  },
};
</script>

